body {
  margin: 0;
  padding: 0;
  font-family: "Courier New", Courier, monospace;
  background: linear-gradient(90deg, #000046 0%, #1CB5E0 100%);
}

button {
  cursor: pointer;
}

.bounding-box {
  position: absolute;
  box-shadow: 0 0 0 3px #149df2 inset;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: center;
          justify-content: center;
  cursor: pointer;
  font-size: small;
 
}

.showinfo { 
  display: none;
  color: white;
  background-color: black;
  opacity: .7;
  margin-top: 110%;

}

.bounding-box:hover .showinfo {
  display : block;
}

.inthebox {
	margin: auto;
}
.Tilt {
  background: linear-gradient(90deg, #FF5EDF 0%, #04C8DE 100%); 
}
.form {
  width: 700px;
  background: #283E51;
  background-size:40px 60px;
}

input:focus::-webkit-input-placeholder { color:transparent; }
input:focus:-moz-placeholder { color:transparent; } /* FF 4-18 */
input:focus::-moz-placeholder { color:transparent; } /* FF 19+ */
input:focus:-ms-input-placeholder { color:transparent; }
.footer {
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  font-size: x-small;
  text-align: right;
}
.App {
  text-align: center;
}

.center {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
}

.particles {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: -1;
}

